"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  Legend,
} from "recharts";

export default function ProjectReport() {
  // Dummy data
  const projectData = {
    projectName: "DevOps Automation Platform",
    teams: [
      { name: "Backend", completed: 25, total: 30 },
      { name: "Frontend", completed: 20, total: 25 },
      { name: "QA", completed: 15, total: 20 },
      { name: "DevOps", completed: 10, total: 15 },
    ],
    tasks: [
      { id: "T-001", title: "Setup CI/CD", status: "Completed" },
      { id: "T-002", title: "Deploy Backend API", status: "In Progress" },
      { id: "T-003", title: "Frontend Dashboard", status: "Pending" },
      { id: "T-004", title: "Test Cases", status: "Completed" },
    ],
    subtasks: [
      { id: "ST-001", taskId: "T-001", title: "Configure Jenkins", status: "Completed" },
      { id: "ST-002", taskId: "T-002", title: "Setup API Routes", status: "In Progress" },
      { id: "ST-003", taskId: "T-003", title: "Design UI Components", status: "Pending" },
    ],
    bugs: [
      { id: "B-001", title: "API timeout issue", status: "Open" },
      { id: "B-002", title: "UI button not clickable", status: "Resolved" },
      { id: "B-003", title: "Incorrect validation", status: "Critical" },
    ],
    moms: [
      { id: "MOM-001", title: "Sprint Planning", date: "2025-09-01" },
      { id: "MOM-002", title: "Retrospective", date: "2025-09-08" },
    ],
  };

  // Charts data preparation
  const bugChartData = [
    { name: "Open", value: projectData.bugs.filter(b => b.status === "Open").length },
    { name: "Resolved", value: projectData.bugs.filter(b => b.status === "Resolved").length },
    { name: "Critical", value: projectData.bugs.filter(b => b.status === "Critical").length },
  ];
  const bugColors = ["#f59e0b", "#10b981", "#ef4444"];

  const teamProgressData = projectData.teams.map(team => ({
    name: team.name,
    completion: (team.completed / team.total) * 100,
  }));

  const areaChartData = projectData.tasks.map((task, i) => ({
    name: task.title,
    Completed: task.status === "Completed" ? 1 : 0,
    Pending: task.status === "Pending" ? 1 : 0,
    InProgress: task.status === "In Progress" ? 1 : 0,
  }));

  return (
    <div className="space-y-6 p-6 max-w-6xl mx-auto">
      {/* Project Title */}
      <h2 className="text-2xl font-bold mb-4">{projectData.projectName} - Report</h2>

      {/* Project Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardHeader>
            <CardTitle>Teams</CardTitle>
          </CardHeader>
          <CardContent>
            <Badge variant="secondary">{projectData.teams.length}</Badge>
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Tasks</CardTitle>
          </CardHeader>
          <CardContent>
            <Badge variant="secondary">{projectData.tasks.length}</Badge>
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Subtasks</CardTitle>
          </CardHeader>
          <CardContent>
            <Badge variant="secondary">{projectData.subtasks.length}</Badge>
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Bugs</CardTitle>
          </CardHeader>
          <CardContent>
            <Badge variant="secondary">{projectData.bugs.length}</Badge>
          </CardContent>
        </Card>
      </div>

      <Separator />

      {/* Team Progress */}
      <Card>
        <CardHeader>
          <CardTitle>Team Progress</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {teamProgressData.map(team => (
            <div key={team.name} className="space-y-1">
              <div className="flex justify-between text-sm">
                <span>{team.name}</span>
                <span>{Math.round(team.completion)}%</span>
              </div>
              <Progress value={team.completion} />
            </div>
          ))}
        </CardContent>
      </Card>

      {/* Bug Status Pie Chart */}
      <Card>
        <CardHeader>
          <CardTitle>Bug Status</CardTitle>
        </CardHeader>
        <CardContent className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Pie
                data={bugChartData}
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                outerRadius={80}
                label
              >
                {bugChartData.map((entry, index) => (
                  <Cell key={index} fill={bugColors[index]} />
                ))}
              </Pie>
              <Legend verticalAlign="bottom" height={36} />
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>

      {/* Task Status Area Chart */}
      <Card>
        <CardHeader>
          <CardTitle>Tasks Overview</CardTitle>
        </CardHeader>
        <CardContent className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <AreaChart data={areaChartData} margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Area type="monotone" dataKey="Completed" stackId="1" stroke="#10b981" fill="#d1fae5" />
              <Area type="monotone" dataKey="InProgress" stackId="1" stroke="#facc15" fill="#fef3c7" />
              <Area type="monotone" dataKey="Pending" stackId="1" stroke="#f87171" fill="#fee2e2" />
            </AreaChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>

      {/* Tasks Table */}
      <Card>
        <CardHeader>
          <CardTitle>Task List</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Task ID</TableHead>
                <TableHead>Title</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {projectData.tasks.map(task => (
                <TableRow key={task.id}>
                  <TableCell>{task.id}</TableCell>
                  <TableCell>{task.title}</TableCell>
                  <TableCell>
                    <Badge variant={
                      task.status === "Completed" ? "success" :
                      task.status === "In Progress" ? "secondary" :
                      "destructive"
                    }>
                      {task.status}
                    </Badge>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {/* MOM Table */}
      <Card>
        <CardHeader>
          <CardTitle>Minutes of Meeting (MOM)</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>ID</TableHead>
                <TableHead>Title</TableHead>
                <TableHead>Date</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {projectData.moms.map(mom => (
                <TableRow key={mom.id}>
                  <TableCell>{mom.id}</TableCell>
                  <TableCell>{mom.title}</TableCell>
                  <TableCell>{mom.date}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      <div className="text-center">
        <Button>Download Full Report</Button>
      </div>
    </div>
  );
}
